import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import Web3Service from "../screens/web3-development-service/web3-service";

const Web3Development = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Web3 Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <Web3Service db={db} />
    </Layout>
  );
};
export const Head = (props) => {
  const { data } = props;
  const fileUrl =
    data?.web3GameDevImg?.childImageSharp?.gatsbyImageData?.images?.fallback
      ?.src;
  // console.log("fileUrl::", fileUrl);
  return (
    <Seo
      title="Web3 Development Company | Justtry Technologies"
      metaTitle="Web3 Development Company | Web3 Development Services"
      description="Justtry Tech, a leading web3 development company, specialize in blockchain development, smart contracts, and decentralized app development."
      image={fileUrl}
    ></Seo>
  );
};

export default Web3Development;

export const queryData = graphql`
  query MyQuery {
    web3GameDevImg: file(relativePath: { eq: "web3-game-development.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
